@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap");
@font-face {
  font-family: "Telegraf Regular";
  src: url("../img/fonts/PPTelegraf-Regular.woff2") format("woff2");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.4em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  font-family: "Inter", sans-serif;
  font-weight: 400;
  color: #050505;
}

html {
  scroll-behavior: smooth;
}

h1 {
  text-transform: uppercase;
  letter-spacing: -0.02em;
  font-size: 52px;
  line-height: 110%;
  font-family: "Telegraf Regular";
}

h2 {
  text-transform: uppercase;
  letter-spacing: -0.02em;
  font-size: 39px;
  line-height: 110%;
  font-family: "Telegraf Regular";
}

a.underlined p {
  position: relative;
  z-index: 1;
}
a.underlined p::after {
  content: "";
  position: absolute;
  height: 10px;
  width: 100%;
  background-color: #FEFEFE;
  left: 0;
  bottom: 3px;
  z-index: -1;
}
a.underlined:hover p {
  color: #EE6F2D;
}

.container {
  width: 1440px;
  margin: 0 auto;
}
.container .intro {
  overflow: hidden;
}
.container .intro nav {
  display: flex;
  align-items: center;
  margin: 48px 80px 132px;
}
.container .intro nav hr {
  border-top: 1px solid #525252;
  margin-right: 64px;
  flex-grow: 1;
}
.container .intro nav ul {
  display: flex;
  list-style-type: none;
  justify-content: flex-end;
}
.container .intro nav ul li {
  margin-left: 20px;
  margin-right: 20px;
  font-size: 25px;
  letter-spacing: -0.01em;
  line-height: 130%;
}
.container .intro nav ul li a {
  color: #050505;
}
.container .intro nav ul li:first-child {
  margin-left: 0;
}
.container .intro nav ul li:last-child {
  margin-right: 0;
}
.container .intro nav ul li.active a {
  font-weight: 700;
}
.container .intro .display {
  font-family: "Telegraf Regular";
  font-size: 148px;
  letter-spacing: -0.04em;
  margin: 0 80px 72px 90px;
  line-height: 100%;
}
.container .intro .display .scroller {
  position: relative;
  height: 148px;
  width: 700px;
  display: inline-block;
  overflow: hidden;
  top: 34px;
  letter-spacing: -0.04em;
}
.container .intro .display .scroller .elements {
  position: absolute;
  top: 0;
  line-height: 100%;
  animation: slide 7s forwards, display 7s forwards;
}
.container .intro .display .scroller .elements p {
  font-family: "Telegraf Regular";
  font-size: 148px;
  line-height: 100%;
  position: relative;
}
.container .intro .display .scroller .elements p:after {
  content: "";
  position: absolute;
  height: 15px;
  width: 100%;
  background-color: #EE6F2D;
  bottom: 26px;
  z-index: -1;
  left: 8px;
  animation: width 7s forwards, display 7s forwards;
}
.container .intro .stack-bar {
  display: flex;
  margin-bottom: 101px;
  animation: stack 20s infinite linear;
  animation-delay: 6s;
}
.container .intro .stack-bar p {
  flex-shrink: 0;
  font-family: "Telegraf Regular";
  font-size: 34px;
  line-height: 120%;
  margin: 0 32px;
  position: relative;
  letter-spacing: -0.01em;
  color: #050505;
}
.container .intro .stack-bar p::after {
  content: "•";
  position: absolute;
  right: -38px;
}
.container .intro .scroll-down-btn {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 0 auto 60px;
  width: fit-content;
}
.container .intro .scroll-down-btn p {
  font-size: 25px;
  margin-bottom: 8px;
  line-height: 130%;
  letter-spacing: -0.01em;
}
.container .intro .scroll-down-btn img {
  position: relative;
  transition: all 0.2s;
  top: 0;
}
.container .intro .scroll-down-btn:hover p {
  color: #EE6F2D;
}
.container .intro .scroll-down-btn:hover img {
  top: 5px;
}
@keyframes stack {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-100%);
  }
}
@keyframes slide {
  0% {
    top: 0;
  }
  24% {
    top: 0;
  }
  25% {
    top: -148px;
  }
  49% {
    top: -148px;
  }
  50% {
    top: -296px;
  }
  74% {
    top: -296px;
  }
  75% {
    top: -444px;
  }
  100% {
    top: -444px;
  }
}
@keyframes display {
  5% {
    opacity: 1;
  }
  15% {
    opacity: 1;
  }
  20% {
    opacity: 0;
  }
  25% {
    opacity: 0;
  }
  30% {
    opacity: 1;
  }
  40% {
    opacity: 1;
  }
  45% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  55% {
    opacity: 1;
  }
  65% {
    opacity: 1;
  }
  70% {
    opacity: 0;
  }
  75% {
    opacity: 0;
  }
  80% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes width {
  0% {
    width: 370px;
  }
  24% {
    width: 370px;
  }
  25% {
    width: 539px;
  }
  49% {
    width: 539px;
  }
  50% {
    width: 584px;
  }
  74% {
    width: 584px;
  }
  75% {
    width: 370px;
  }
  100% {
    width: 370px;
  }
}
